import path from 'path';
import { safeReadJsonSync } from './io';

export const isPartnerApp = () => {
    return process.env.MSDyn365_APP_TYPE && process.env.MSDyn365_APP_TYPE === 'partner';
};

export interface IPlatformSettings {
    namespaceExtensions: string[];
}

export const getPlatformSettings = (): IPlatformSettings => {
    return (
        safeReadJsonSync(path.resolve(process.cwd(), 'src', 'settings', 'platform.settings.json')) || {
            namespaceExtensions: []
        }
    );
};

export const getSupportedNamespaces = () => {
    const namespaceExtensions = getPlatformSettings().namespaceExtensions;
    return namespaceExtensions ? ['@msdyn365-commerce-modules'].concat(namespaceExtensions) : ['@msdyn365-commerce-modules'];
};
